.header{
  background-color: $black;
  color: #fff;
  position: relative;
  padding-bottom: 3.75em;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5625em 0;
  }
  &__logo{
    display: block;
    width: 11.625em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__cart{
    display: flex;
    align-items: center;
    flex-shrink: 0;
    img{
      width: 3.125em;
      flex-shrink: 0;
    }
    &-pin{
      min-width: 1.25em;
      height: 1.25em;
      padding: 0 .25em;
      @include center;
      border-radius: 50%;
      background-color: $primary;
      color: #fff;
      margin-left: .3125em;
      text-shadow: 0 .0625em .125em rgba(0, 0, 0, 0.1);
      span{
        font-size: .75em;
        letter-spacing: 0.03em;
      }
    }
  }
}

.menu{
  display: flex;
  align-items: center;
}

.header-info{
  display: flex;
  align-items: center;
  &__item{
    display: flex;
    align-items: center;
    @include lastM(0 8.75em 0 0);
    img{
      height: 1.25em;
      flex-shrink: 0;
      display: block;
      margin-right: .625em;
    }
    span{
      font-size: 1.375em;
      line-height: 150%;
      letter-spacing: 0.01em;
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    @include center;
    width: 2.5em;
    height: 2.5em;
    flex-shrink: 0;
    margin-left: 1.25em;
    border-radius: 50%;
    background: #121314;
    img{
      width: 1.25em;
    }
    &:hover{
      background: $primary;
    }
  }
}

.nav{
  background: #121314;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  &__list{
    height: 3.75em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__link{
    letter-spacing: 0.03em;
    text-transform: uppercase;
    font-weight: 700;
    padding: .25em 0;
    display: flex;
    border-bottom: 1px solid transparent;
    position: relative;
    &._active{
      border-color: $primary;
      color: $primary;
      &:before{
        opacity: 1;
      }
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
    &:before{
      content: '';
      position: absolute;
      top: 100%;
      left: 50%;
      transform: translate(-50%,0);
      width: .625em;
      height: .3125em;
      background-image: url(../img/triangle.svg);
      @include bgImgSize(100%);
      transition: inherit;
      opacity: 0;
    }
  }
}

.burger{
  display: none;
}



@media screen and (max-width: 992px){
  .header{
    padding: 0;
    &__inner{
      padding: 1.25em 0;
    }
    &__logo{
      width: 10em;
    }
    &__buttons{
      display: flex;
      align-items: center;
    }
    &__cart{
      img{
        width: 2.5em;
      }
    }
    &:before{
      content: '';
      position: fixed;
      left: 0;
      bottom: 0;
      width: 100%;
      height: calc(100 * var(--vh) - 6.25em);
      background-color: $black;
      opacity: 0;
      pointer-events: none;
      transform: translateX(-200%);
      transition: all .4s;
    }
    &._open{
      .burger{
        background-image: url(../img/burger2.svg);
        background-size: 1.25em auto;
      }
      &:before,
      .menu,
      .nav{
        transform: translateX(0%);
        opacity: 1;
        pointer-events: unset;
      }
      .nav{
        transform: translateX(-50%);
      }
    }
  }

  .menu{
    flex-grow: 1;
    margin: 0 3em;
    justify-content: space-between;
  }

  .header-info{
    align-items: flex-start;
    flex-direction: column;
    &__item{
      @include lastM(0 0 .9375em);
      span{
        font-size: .875em;
      }
      &-icon{
        width: 1em;
        flex-shrink: 0;
        margin-right: .625em;
        img{
          height: 1em;
          margin: 0;
        }
      }
    }
  }

  .socials{
    &__item{
      width: 1.875em;
      height: 1.875em;
      img{
        width: .9375em;
      }
    }
  }

  .burger{
    z-index: 11;
    position: relative;
    display: block;
    flex-shrink: 0;
    border: 1px solid #FFFFFF;
    width: 2.5em;
    height: 2.5em;
    border-radius: .1875em;
    margin-left: 3em;
    background-image: url(../img/burger.svg);
    @include bgImgSize(1.625em);
  }

  @media screen and (min-width: 577px){
    .nav{
      position: fixed;
      height: $fullHeight;
      top: 0;
      left: 50%;
      width: 100vw;
      background: rgba($black,.8);
      z-index: 10;
      opacity: 0;
      pointer-events: none;
      transform: translateX(-200%);
      transition: all .4s;
      padding: 6.25em 2.5em 2.5em;
      overflow-y: auto;
    }
  }

  .nav{
    .m-container{
      padding: 0;
    }
    &__list{
      height: unset;
      flex-direction: column;
    }
    &__item{
      height: 2.5em;
      @include center;
      text-align: center;
    }
    &__link{
      padding: 0;
      border: none;
      &:before{
        display: none;
      }
      span{
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__inner{
      padding: .75em 0;
    }
    &__logo{
      width: 7.625em;
    }
    &__cart{
      img{
        width: 1.875em;
      }
      &-pin{
        min-width: 1.125em;
        height: 1.125em;
        span{
          font-size: .625em;
        }
      }
    }
    &:before{
      height: calc(100 * var(--vh) - 4.375em);
    }
    &._open{
      .nav{
        transform: unset;
      }
    }
  }

  .burger{
    margin-left: 2em;
  }

  .menu{
    margin: 0;
    position: fixed;
    height: $fullHeight;
    overflow-y: auto;
    top: 0;
    left: 0;
    width: 100%;
    background: rgba($black,.8);
    z-index: 10;
    opacity: 0;
    pointer-events: none;
    transform: translateX(-200%);
    transition: all .4s;
    padding: 4.375em 2.5em 2.5em;
    flex-direction: column;
  }

  .nav{
    position: static;
    background-color: transparent;
    width: 100%;
  }

  .header-info,
  .socials{
    position: relative;
    z-index: 2;
  }

  .header-info{
    padding: 2.375em 1.25em 0;
    align-items: center;
  }

  .socials{
    justify-content: center;
    width: 100%;
    margin-top: 1.375em;
    &__item{
      margin: 0 .9375em;
      width: 2.5em;
      height: 2.5em;
      img{
        width: 1.25em;
      }
    }
  }
}
