.fg {
  position: relative;
  margin: 0;
  min-height: unset;
  textarea,
  input {
    border: $border;
    border-radius: .1875em;
    padding: 0 1.25em;
    height: 3.125em;
    width: 100%;
    font-size: 1em;
    line-height: 150%;
    background-color: $bg;
    color: $black;
    &::placeholder {
      color: $black2;
    }
    &:focus{
      border-color: $primary;
    }
  }
  textarea{
    height: 9.375em;
    padding-top: .5em;
    padding-bottom: .5em;
    resize: none;
  }
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 2.5em;
    height: 1.875em;
    border-right: $border;
    @include center;
    img{
      max-width: 1.25em;
      max-height: 1.25em;
    }
    &~input{
      padding-left: 3.1875em;
    }
  }
}

.ui-timepicker-standard{
  border-color: $primary;
  transform: translateY(5px);
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}


@media screen and (max-width: 992px){
  .fg{
    textarea{
      height: 7.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .fg{
    textarea,
    input{
      font-size: .6875em;
      height: 3.64em;
    }
    textarea{
      height: 18.18em;
    }
    &__icon{
      width: 2.25em;
      height: 1.5ems;
      &~input{
        padding-left: 4.27em;
      }
      img{
        max-width: 1em;
        max-height: 1em;
      }
    }
  }
}
