@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";

._line-before{
  position: relative;
  text-align: center;
  padding-bottom: .25em;
  &:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 6.25em;
    height: 1px;
    background-color: $primary;
  }
}

._white-bg{
  background-color: #fff;
}

.cartcalc{
  button{
    background-color: #fff;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 1.875em;
    height: 1.875em;
    border-radius: .1875em;
    box-shadow: 0 0 .3125em rgba(0, 0, 0, 0.1);
    &:hover{
      background-color: $primary;
      border-color: $primary;
      path,
      rect{
        fill: #fff;
      }
    }
    svg{
      width: 1em;
      height: auto;
    }
  }
  .ccalc-minus{
    left: .3125em;
  }
  .ccalc-plus{
    right: .3125em;
  }
  input{
    height: 2.5em;
    padding: 0 2.5em;
    font-weight: 700;
    text-align: center;
  }
}


@media screen and (max-width: 576px){
  ._line-before{
    &:before{
      width: 3.125em;
    }
  }

  .cartcalc{
    input{
      font-size: .875em;
      height: 2.14em;
    }
    button{
      width: 1.25em;
      height: 1.25em;
      svg{
        width: .75em;
      }
    }
  }
}
