@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    border-radius: .1875em;
    height: 3.125em;
    &:focus {
        box-shadow: none;
    }
    font-weight: 700;
    span{
        font-size: 1em;
        line-height: 150%;
        letter-spacing: 0.03em;
        text-transform: uppercase;
    }
}

.m-btn-primary {
    background: $primary;
    box-shadow: inset 0 -.1875em 0 rgba(0, 0, 0, 0.1);
    color: $black;
    &:hover{
        color: $black;
    }
}

.m-btn-outline{
    padding: 0 1.875em;
    border: 1px solid $black2;
    color: $black;
    &:hover{
        color: $black;
        background-color: $primary;
        border-color: $primary;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        span{
            font-size: .875em;
        }
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        height: 2.5em;
        span{
            font-size: .6875em;
        }
    }
}
