.basket {
  padding: 0 0 6.25em;
  &__content {
    background: #FFFFFF;
    border: 0.0625em solid #E4E5E6;
    box-shadow: 0 0.3125em 1.5625em rgba(0, 0, 0, 0.1);
    border-radius: 0.1875em;
    padding: 1.875em;
    margin-top: 1.875em;
  }
  &-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.25em 0.625em;
    position: relative;
    border-top: 0.0625em solid #E4E5E6;
    &:last-child {
      border-bottom: 0.0625em solid #E4E5E6;
    }
    &__right {
      display: flex;
      align-items: center;
      margin-left: 0.9375em;
    }
    &__title {
      font-size: 1em;
      line-height: 150%;
      font-weight: 700;
      letter-spacing: 0.01em;
    }
    &__price {
      font-size: 1em;
      line-height: 150%;
      font-weight: 700;
      letter-spacing: 0.01em;
    }
    .cartcalc {
      width: 8em;
      margin: 0 1.25em;
    }
    &__amount {
      font-size: 1em;
      line-height: 150%;
      font-weight: 700;
      letter-spacing: 0.01em;
    }
    &__delete {
      flex-shrink: 0;
      width: 1.875em;
      height: 1.875em;
      background: #F8F9FA;
      border-radius: 50%;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0;
      margin-left: 6.25em;
      &:hover{
        background: $primary;
        path{
          fill: #fff;
        }
      }
    }
  }
  &__result {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding-top: 1.875em;
    &-text {
      p {
        font-size: 1.375em;
        line-height: 150%;
        font-weight: 700;
        letter-spacing: 0.01em;
      }
    }
    &-btn {
      padding: 0 1.875em;
      margin-left: 1.875em;
    }
  }
}

@media screen and (max-width: 992px) {
  .basket {
    padding:  0 0 2.5em;
    &__content {
      padding: 1.25em;
      margin-top: 1.25em;
    }
    &-item {
      &__title {
        font-size: 0.875em;
      }
      &__price {
        font-size: 0.875em;
      }
      .cartcalc {
        margin: 0 0.625em;
      }
      &__amount {
        font-size: 0.875em;
      }
      &__delete {
        width: 1.25em;
        height: 1.25em;
        margin-left: 1.25em;
        svg {
          width: 0.4375em;
          height: auto;
        }
      }
    }
    &__result {
      padding-top: 1.25em;
      &-text {
        p {
          font-size: 1.25em;
        }
      }
      &-btn {
        margin-left: 1.25em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .basket {
    &__content {
      padding: 1.25em 0.625em;
    }
    &-item {
      display: block;
      padding: 0.4375em 1.875em 0.4375em 0.625em;
      position: relative;
      &__right {
        display: block;
        margin: 0.25em 0 0 0;
      }
      &__title {
        font-size: 0.6875em;
      }
      &__price {
        font-size: 0.6875em;
      }
      .cartcalc {
        width: 6.875em;
        margin: 0.4375em 0;
      }
      &__amount {
        font-size: 0.6875em;
      }
      &__delete {
        position: absolute;
        top: 0.125em;
        right: 0;
        margin: 0;
      }
    }
    &__result {
      flex-direction: column;
      text-align: center;
      &-text {
        p {
          font-size: 0.875em;
          &:not(:first-child) {
            margin-top: 0.5625em;
          }
        }
      }
      &-btn {
        width: 100%;
        margin: 0.875em 0 0 0;
      }
    }
  }
}
