.services{
  padding: 0 0 2.625em;
  &__text{
    margin-top: 1.5em;
    text-align: center;
    p{
      line-height: inherit;
    }
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
    gap: 1.875em;
    margin-top: 1.5em;
  }
}

.service-card{
  padding: 1.5em 1.875em 1.875em;
  background-color: $vinous;
  color: #FFFFFF;
  display: flex;
  flex-direction: column;
  text-align: center;
  border-radius: .1875em;
  &__info{
    width: 100%;
    padding-bottom: .875em;
    flex-grow: 1;
    @include center;
    flex-direction: column;
  }
  .text22,
  .text16{
    line-height: 130%;
  }
  &__nav{
    margin-top: 1.25em;
  }
  &__btn{
    width: 100%;
    height: 2.5em;
  }
}

.soar{
  padding: 2.625em 0 3.125em;
  &__items{
    margin-top: 1.875em;
    @include grid100gap(.625em);
  }
}

.soar-card{
  padding: 1.875em 1.875em 1.5em;
  background-color: #fff;
  border-radius: .1875em;
  border: $border;
  display: grid;
  grid-template-columns: 11.625em calc(100% - 26.5em) 12.375em;
  gap: 1.25em;
  align-items: flex-start;
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .1875em;
    }
  }
  &__info,
  &__top{
    @include grid100gap(1.125em);
  }
  &__info{
    max-width: 49.625em;
  }
  &__action{
    padding-top: 1.75em;
    text-align: center;
  }
  &__btn{
    margin-top: .75em;
    width: 100%;
    height: 2.5em;
  }
}

.services2{
  padding: 3.125em 0 6.25em;
  &__items{
    @include grid100gap(.625em);
  }
}

.spoiler-card{
  background-color: #fff;
  box-shadow: $shadow;
  border-radius: .1875em;
  &__show{
    padding: 1.125em 1.875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    &:hover{
      path{
        fill: $primary;
      }
    }
    &._active{
      .spoiler-card__btn{
        svg{
          transform: rotate(-180deg);
        }
      }
    }
  }
  &__btn{
    width: 1.75em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
      transition: all .4s;
    }
  }
  &__hidden{
    padding: 0 1.875em 1.875em;
    display: none;
  }
}

.service-card2{
  padding: 1.375em .625em;
  border-top: $border;
  border-bottom: $border;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &:not(:first-child){
    margin-top: -1px;
  }
  &__action{
    display: flex;
    align-items: center;
    flex-shrink: 0;
    white-space: nowrap;
  }
  &__btn{
    width: 12.375em;
    height: 2.5em;
    flex-shrink: 0;
    margin-left: 1.875em;
  }
}


@media screen and (max-width: 992px){
  .services{
    padding-bottom: 1.5em;
    &__text{
      margin-top: 1em;
    }
    &__grid{
      margin-top: .875em;
      grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
      gap: 1.25em;
    }
  }

  .service-card{
    padding: .5em .625em 1.25em;
    &__info{
      padding-bottom: .5em;
    }
    .text22{
      font-size: .875em;
    }
    .text16{
      font-size: .75em;
    }
    &__btn{
      height: 1.875em;
    }
  }

  .soar{
    padding: 3em 0 1.875em;
    &__items{
      margin-top: 1.25em;
    }
  }

  .soar-card{
    grid-template-columns: calc(100% - 11.25em) 10em;
    gap: .875em 1.25em;
    padding: 1.25em;
    position: relative;
    &__img{
      grid-column: 1/-1;
      height: 6.25em;
      img{
        width: 8.25em;
        height: 100%;
        object-fit: cover;
      }
    }
    &__action{
      padding: 0;
    }
    @media screen and (min-width: 577px){
      &__top{
        position: absolute;
        height: 6.25em;
        left: 10.75em;
        top: 1.25em;
        display: flex;
        gap: unset;
        flex-direction: column;
        justify-content: center;
      }
      &__title{
        margin-bottom: .5em;
      }
    }
  }

  .services2{
    padding: 1.875em 0 3.75em;
  }

  .spoiler-card{
    &__show{
      padding: .75em 1.25em;
    }
    &__btn{
      width: 1.3125em;
    }
    &__hidden{
      padding: 0 1.25em 1.25em;
    }
  }

  .service-card2{
    padding-top: 1em;
    padding-bottom: 1em;
    &__btn{
      width: 10em;
      margin-left: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .services{
    padding-bottom: 1em;
    &__grid{
      margin-top: 1em;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: 1.25em .625em;
    }
  }

  .service-card{
    padding-top: 1em;
    &__info{
      width: calc(100% + .625em);
      margin-left: -.3125em;
    }
  }

  .soar{
    padding: 1em 0 1.25em;
  }

  .soar-card{
    padding: 1.25em .625em;
    grid-template-columns: 100%;
    gap: 1em;
    &__img{
      height: 4.6875em;
      img{
        width: 6.25em;
      }
    }
    &__info{
      gap: .6875em;
    }
    &__title{
      position: absolute;
      height: 5.35em;
      width: calc(100% - 9.28em);
      top: 1.42em;
      right: .71em;
      display: flex;
      align-items: center;
    }
  }

  .service-card2,
  .soar-card{
    &__action{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
      align-items: center;
    }
    &__btn{
      margin: 0;
      height: 1.875em;
    }
  }

  .spoiler-card{
    &__show{
      padding: 1em .625em;
    }
    &__btn{
      width: 1.125em;
    }
    &__hidden{
      padding: 0 .625em;
    }
  }

  .services2{
    padding: 1.25em 0 2.5em;
  }

  .service-card2{
    @include grid100gap(.875em);
    padding: 1em 0 1.25em;
    &__action{
      text-align: center;
    }
    &__btn{
      width: 100%;
    }
  }
}
