.order{
  padding-bottom: 6.25em;
  &__nav{
    margin-top: 1.875em;
  }
  &__btn{
    width: 100%;
  }
}

.order-info{
  padding: 1.375em 1.875em 1.875em;
  background: #FFFFFF;
  border: $border;
  box-shadow: $shadow;
  border-radius: .1875em;
  margin-top: 1.875em;
  &__top{
    padding-bottom: .75em;
    text-transform: uppercase;
    border-bottom: $border;
    .text22{
      letter-spacing: 0.03em;
    }
  }
  &__fields{
    margin-top: .25em;
    width: calc(100% + 1.875em);
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  &__field{
    margin: 1.25em 1.875em 0 0;
  }
  &__surname,
  &__name{
    width: 20em;
  }
  &__phone{
    width: 12em;
  }
  &__date{
    width: 9em;
  }
  &__time{
    width: calc(100% - 61em - 9.375em);
  }
  &__calc{
    width: 12.5em;
    .cartcalc{
      margin-top: .25em;
    }
    input{
      height: 3.125em;
    }
  }
}

.order-comment{
  margin-top: 1.25em;
}


@media screen and (max-width: 992px){
  .order{
    padding-bottom: 3.75em;
  }

  .order-info{
    padding: .875em 1.25em 1.25em;
    margin-top: 1.25em;
    &__top{
      padding-bottom: .125em;
    }
    &__fields{
      justify-content: unset;
      align-items: flex-end;
      width: calc(100% + 1.25em);
    }
    &__field{
      margin: 1em 1.25em 0 0;
    }
    &__name,
    &__surname,
    &__phone{
      width: calc(100% / 3 - 1.25em);
    }
    &__date,
    &__time{
      width: 7.5em;
    }
    &__calc{
      width: calc(50% - (17.5em / 2) - 1.25em);
    }
  }
}


@media screen and (max-width: 576px){
  .order{
    padding-bottom: 2.5em;
    &__nav{
      margin-top: 1.25em;
    }
  }

  .order-info{
    padding-top: 1em;
    &__fields{
      width: calc(100% + .625em);
    }
    &__field{
      margin: 1em .625em 0 0;
    }
    &__name,
    &__surname,
    &__phone,
    &__calc{
      width: calc(100% - .625em);
    }
    &__date,
    &__time{
      width: calc(50% - .625em);
    }
    &__calc{
      .cartcalc{
        margin-top: .3125em;
        input{
          height: 2.85em;
        }
        button{
          width: 1.875em;
          height: 1.875em;
          svg{
            width: 1em;
          }
        }
      }
    }
  }
}
