.modal-backdrop {
    &.show {
        opacity: 1;
        background: rgba(70, 65, 65, 0.5);
    }
}

.modal{
    text-align: center;
    @media screen and (min-width: 577px){
        &:before {
            display: inline-block;
            vertical-align: middle;
            content: " ";
            height: 100%;
        }
    }

    &__close{
        width: 1.5625em;
        position: absolute;
        top: .75em;
        right: .625em;
        svg{
            width: 100%;
            height: auto;
        }
        &:hover{
            line{
                stroke: $primary;
            }
        }
    }
    &__title{
        font-size: 1.25em;
        line-height: 150%;
        text-align: center;
        font-weight: 700;
        text-transform: uppercase;
    }
    &__nav{
        margin-top: 2.25em;
        display: flex;
        justify-content: center;
    }
    &__btn{
        width: 13.75em;
        max-width: 100%;
    }
}

.modal-dialog{
    margin: 1em auto;
    pointer-events: unset;
    width: 34em;
    max-width: calc(100% - 1.5em);
    padding: 3.5em 2em 3.3125em;
    background-color: #fff;
    @media screen and (min-width: 577px){
        display: inline-block;
        vertical-align: middle;
        text-align: left;
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__title{
            font-size: 1em;
        }
        &__nav{
            margin-top: 1.25em;
        }
        &__btn{
            height: 3.125em;
            span{
                font-size: 1em;
            }
        }
    }

    .modal-dialog{
        padding: 3.125em 1em;
    }
}
