.promos {
  padding: 0 0 6.25em;
  &__grid {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
    margin-top: 1.875em;
  }
}

.promo-card {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #FFFFFF;
  border: 0.0625em solid #E4E5E6;
  border-radius: 0.1875em;
  box-shadow: 0 0.3125em 1.5625em rgba(0, 0, 0, 0.1);
  padding: 1.875em;
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    margin-left: 0.75em;
  }
  &__img {
    flex-shrink: 0;
    width: 8.25em;
    img {
      display: block;
      width: 100%;
    }
  }
  &__desc {
    margin-left: 1.875em;
  }
  &__title {
    font-size: 1.375em;
    line-height: 150%;
    font-weight: 700;
    letter-spacing: 0.01em;
  }
  &__subtitle {
    font-size: 1em;
    line-height: 150%;
    letter-spacing: 0.01em;
  }
  &__price {
    font-size: 1.375em;
    line-height: 150%;
    font-weight: 700;
    letter-spacing: 0.01em;
    text-align: center;
  }
  &__btn {
    margin-top: 0.75em;
    padding: 0 1.875em;
  }
}


@media screen and (max-width: 992px) {
  .promos {
    padding: 0 0 2.5em;
    &__grid {
      margin-top: 1.25em;
    }
  }

  .promo-card {
    padding: 1.25em;
    &__desc {
      margin-left: 1.25em;
    }
    &__title {
      font-size: 1.25em;
    }
    &__subtitle {
      font-size: 1.25em;
    }
    &__price {
      font-size: 1.25em;
    }
    &__btn {
      height: 2.5em;
      padding: 0 1.25em;
    }
  }
}

@media screen and (max-width: 576px) {
  .promo-card {
    display: block;
    padding: 1.25em 0.625em;
    &__right {
     display: grid;
      grid-template-columns: repeat(2, calc(50% - 0.75em / 2));
      gap: 0.75em;
      align-items: center;
      margin-top: 1.25em;
    }
    &__img {
      width: 6.25em;
    }
    &__desc {
      margin-left: 0.625em;
    }
    &__title {
      font-size: 0.875em;
    }
    &__subtitle {
      font-size: 0.6875em;
    }
    &__price {
      font-size: 0.875em;
    }
    &__btn {
      height: 1.875em;
      margin: 0;
      padding: 0 0.75em;
    }
  }
}