@import "../vars.scss";
.breadcrumbs {
    padding: 1.5625em 0 1.8125em;
    &__item {
        font-size: .75em;
        line-height: 150%;
        position: relative;
        font-weight: 400;
        display: inline;
        letter-spacing: 0.01em;
        color: $black2;
        &:last-child {
            color: $black;
        }
        &:not(:first-child) {
            &:before {
                content: '»';
                margin: 0 .5em;
                display: inline-block;
            }
        }
    }
}


@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: .9375em 0 .25em;
        &__item{
            font-size: .6875em;
        }
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        padding: 1em 0 .625em;
    }
}
