.menu-page{
  padding-bottom: 6.25em;
  &__links{
    display: flex;
    flex-wrap: wrap;
    margin-top: 1.25em;
    width: calc(100% + .625em);
  }
  &__link{
    @include center;
    margin: .625em .625em 0 0;
    padding: 0 .9375em;
    height: 1.875em;
    border-radius: .1875em;
    background-color: $bg2;
    color: $black;
    &._active,
    &:hover{
      background-color: $primary;
      color: $black;
    }
  }
}

.search{
  margin-top: 1.875em;
  padding: 1.25em;
  border-radius: .1875em;
  border: $outline;
  box-shadow: $shadow;
  position: relative;
  &__btn{
    position: absolute;
    right: 1.25em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__field{
    &._active{
      .search-variants{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
}

.search-variants{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  margin-top: .125em;
  z-index: 2;
  background-color: #fff;
  filter: drop-shadow(0px 5px 25px rgba(0, 0, 0, 0.1));
  &__item{
    min-height: 2.5em;
    display: flex;
    align-items: center;
    padding: .5em 1.25em;
    span{
      font-size: 1em;
      line-height: 150%;
    }
    &:hover{
      color: $black;
      background-color: $primary;
    }
  }
}

.products{
  margin-top: 1.875em;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
  gap: 1.875em;
}

.product-card{
  background: #FFFFFF;
  border-radius: .1875em;
  border: $border;
  display: flex;
  flex-direction: column;
  &__img{
    display: block;
    @include imgCover(75%);
    img{
      border-radius: .1875em .1875em 0 0;
    }
  }
  &__content{
    padding: .75em 1.25em;
    @include grid100gap(.5em);
  }
  &__price{
    margin-top: -.1em;
  }
  &__nav{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
    margin-top: auto;
    padding: 0 1.25em 1.25em;
  }
  &__btn{
    height: 2.5em;
  }
}


@media screen and (max-width: 992px){
  .menu-page{
    padding-bottom: 3.75em;
    &__links{
      margin-top: .625em;
    }
    &__link{
      height: 1.5em;
      padding: 0 .625em;
      span{
        font-size: .875em;
      }
    }
  }

  .search{
    margin-top: 1.25em;
    box-shadow: $shadow2;
  }

  .search-variants{
    &__item{
      min-height: 1.875em;
      span{
        font-size: .875em;
      }
    }
  }

  .products{
    margin-top: 1.25em;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 1.25em;
  }

  .product-card{
    &__content{
      padding: 1em .625em .75em;
      gap: .625em;
    }
    &__title{
      line-height: 130%;
      font-size: .875em;
    }
    &__price{
      margin-top: -.2em;
    }
    &__nav{
      grid-template-columns: 100%;
      gap: 1.25em;
      padding: 0 .625em 1.25em;
    }
  }
}



@media screen and (max-width: 576px){
  .menu-page{
    padding-bottom: 2.5em;
    &__link{
      height: 1.3125em;
      span{
        font-size: .6875em;
      }
    }
  }

  .search{
    padding: .625em;
    box-shadow: $shadow3;
    input{
      padding-left: .91em;
    }
    &__btn{
      width: 1em;
      right: .625em;
    }
  }

  .products{
    grid-template-columns: 100%;
    gap: .625em;
  }

  .product-card{
    padding: 1.25em .625em;
    position: relative;
    &__content{
      padding: 0 0 0 6.875em;
      min-height: 4.6875em;
      display: block;
    }
    &__img{
      position: absolute;
      left: .625em;
      top: 1.25em;
      width: 6.25em;
      height: 4.6875em;
      padding: 0;
    }
    &__nav{
      margin-top: .625em;
      padding: 0;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
    &__btn{
      height: 1.875em;
    }
    &__title{
      font-size: .6875em;
    }
    &__weight{
      margin-top: .25em;
    }
  }

  .search-variants{
    &__item{
      padding: 0 .625em;
      span{
        font-size: .6875em;
      }
    }
  }
}
