.events {
  padding: 0 0 6.25em;
  &__grid {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 1.875em / 3));
    gap: 1.875em;
    margin-top: 1.875em;
  }
}

.event-card {
  &__img {
    position: relative;
    padding-bottom: 73%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
    }
  }
  &__desc {
    margin-top: 1.375em;
  }
  &__title {
    font-size: 1.375em;
    line-height: 150%;
    font-weight: 700;
    letter-spacing: 0.01em;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:2;
    -webkit-box-orient: vertical;
  }
  &__link {
    display: flex;
    align-items: center;
    margin-top: 0.875em;
    span {
      font-size: 1em;
      line-height: 150%;
      font-weight: 700;
      letter-spacing: 0.01em;
      color: #D1A15B;
    }
    i {
      display: flex;
      align-items: center;
      margin-left: 0.625em;
    }
  }
}


@media screen and (max-width: 992px) {
  .events {
    padding: 0 0 2.5em;
    &__grid {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
      margin-top: 1.25em;
    }
  }

  .event-card {
    &__desc {
      margin-top: 0.75em;
    }
    &__title {
      font-size: 1.25em;
    }
    &__link {
      span {
        font-size: 0.875em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .events {
    &__grid {
      grid-template-columns: 100%;
      gap: 1.5625em;
    }
  }

  .event-card {
    &__title {
      font-size: 0.875em;
    }
    &__link {
      margin-top: 0.625em;
    }
  }
}