.footer{
  padding: 3em 0;
  background-color: $black;
  color: rgba(#fff,.5);
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

@media screen and (max-width: 992px){
  .footer{
    padding: 1.875em 0;
  }
}

@media screen and (max-width: 576px){
  .footer{
    padding: 1em 0;
    &__inner{
      flex-direction: column;
    }
    &__c,
    &__link{
      @include lastM(0 0 1em);
    }
  }
}
