.product {
  padding: 0 0 6.25em;
  &__inner {
    display: flex;
    background: #FFFFFF;
    border: 0.0625em solid #E4E5E6;
    box-shadow: 0 0.3125em 1.5625em rgba(0, 0, 0, 0.1);
    border-radius: 0.1875em;
    padding: 1.875em;
  }
  &__img {
    width: 44.375em;
    margin-right: 1.875em;
    img {
      display: block;
      width: 100%;
    }
  }
  &__desc {
    width: calc(100% - 46.25em);
  }
  &__text {
    margin-top: 0.9375em;
    p {
      font-size: 1em;
      line-height: 150%;
      letter-spacing: 0.01em;
    }
  }
  &__weight {
    margin-top: 0.9375em;
    span {
      font-size: 1.375em;
      line-height: 150%;
      letter-spacing: 0.01em;
    }
  }
  &__price {
    margin-top: 0.9375em;
    span {
      font-size: 1.875em;
      line-height: 150%;
      font-weight: 700;
      letter-spacing: 0.01em;
    }
  }
  &__btns {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.875em / 2));
    gap: 1.875em;
    margin-top: 0.9375em;
  }
  .cartcalc {
    input {
      height: 100%;
    }
  }
  &__btn {
    span {
      line-height: 105%;
      font-weight: 700;
    }
  }
  &__amount {
    margin-top: 0.9375em;
    span {
      font-size: 1.875em;
      line-height: 150%;
      font-weight: 700;
      letter-spacing: 0.01em;
    }
  }
}

@media screen and (max-width: 992px) {
  .product {
    padding: 0 0 2.5em;
    &__inner {
      padding: 1.25em;
    }
    &__img {
      width: 20.875em;
      margin-right: 1.25em;
    }
    &__desc {
      width: calc(100% - 22.125em);
    }
    &__text {
      margin-top: 0.4375em;
      p {
        font-size: 0.875em;
      }
    }
    &__weight {
      margin-top: 0.4375em;
      span {
        font-size: 1.25em;
      }
    }
    &__price {
      margin-top: 0.4375em;
      span {
        font-size: 1.25em;
      }
    }
    &__btns {
      grid-template-columns: repeat(2,calc(50% - 0.625em / 2));
      gap: 0.625em;
      margin-top: 0.75em;
    }
    &__amount {
      margin-top: 0.75em;
      span {
        font-size: 1.25em;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .product {
    &__inner {
      display: block;
      padding: 0;
    }
    &__img {
      width: 100%;
      margin: 0;
    }
    &__desc {
      padding: 0.9375em 1.25em;
      width: 100%;
    }
    &__text {
      p {
        font-size: 0.6875em;
      }
    }
    &__weight {
      span {
        font-size: 0.875em;
      }
    }
    &__price {
      span {
        font-size: 0.875em;
      }
    }
    &__btns {
      grid-template-columns: repeat(2,calc(50% - 1.25em / 2));
      gap: 1.25em;
    }
    &__btn {
      height: 1.875em;
    }
    &__amount {
      span {
        font-size: 0.875em;
      }
    }
  }
}