.certificates {
  padding: 0 0 6.25em;
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    background: #FFFFFF;
    border: 0.0625em solid #E4E5E6;
    box-shadow: 0 0.3125em 1.5625em rgba(0, 0, 0, 0.1);
    border-radius: 0.1875em;
    padding: 1.5625em 1.875em;
    margin-top: 1.875em;
    p {
      font-size: 1em;
      line-height: 150%;
      letter-spacing: 0.01em;
    }
  }
  &__grid {
    display: grid;
    grid-template-columns: repeat(2, calc(50% - 1.875em / 2));
    gap: 1.875em;
    margin-top: 1.875em;
  }
}

.certificate-card {
  background: #FFFFFF;
  box-shadow: 0 0.3125em 1.5625em rgba(0, 0, 0, 0.1);
  border: 0.0625em solid #E4E5E6;
  border-radius: 0.1875em;
  &__img {
    position: relative;
    padding-bottom: 57%;
    img {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      width: 100%;
      height: 100%;
      border-radius: 0.1875em;
    }
  }
  &__desc {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.375em 1.875em;
  }
  &__title {
    font-size: 1.375em;
    line-height: 150%;
    font-weight: 700;
    letter-spacing: 0.01em;
  }
  &__btn {
    padding: 0 1.875em;
    margin-left: 0.75em;
  }
}


@media screen and (max-width: 992px) {
  .certificates {
    padding: 0 0 2.5em;
    &__text {
      padding: 0.9375em 1.25em;
      margin-top: 1.25em;
      p {
        font-size: 0.875em;
      }
    }
    &__grid {
      grid-template-columns: repeat(2, calc(50% - 1.25em / 2));
      gap: 1.25em;
      margin-top: 1.25em;
    }
  }

  .certificate-card {
    &__desc {
      display: block;
    }
    &__desc {
      padding: 0.9375em 1.25em;
    }
    &__title {
      font-size: 1.25em;
    }
    &__btn {
      width: 100%;
      margin: 0.75em 0 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .certificates {
    &__text {
      p {
        font-size: 0.6875em;
      }
    }
    &__grid {
      grid-template-columns: 100%;
    }
  }

  .certificate-card {
    &__title {
      font-size: 0.8125em;
      br {
        display: none;
      }
    }
    &__btn {
      height: 1.875em;
    }
  }
}