.main{
  position: relative;
  padding: 3.4375em 0;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 28em) 25.625em;
    justify-content: space-between;
  }
  &__img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
    ._view2,
    ._view3{
      display: none;
    }
  }
  &__content{
    padding: 1.75em 0;
    max-width: 35.25em;
    color: #fff;
  }
  &__info{
    margin-top: 1.375em;
    @include grid100gap(2.375em);
  }
  &__desc{
    max-width: 28.75em;
    padding: .75em 0 .75em 1.25em;
    border-left: 1px solid $primary;
  }
}

.main-form{
  padding: 1.875em;
  background-color: #fff;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .625em));
  gap: 1.25em;
  border-radius: .1875em;
  box-shadow: $shadow;
  border: $border;
  ._full{
    grid-column: 1/-1;
  }
  &__btn{
    grid-column: 1/-1;
    width: 100%;
  }
}

.about{
  padding: 5.25em 0 6.25em;
  &__inner{
    margin-top: 1.875em;
    background: #FFFFFF;
    border: $border;
    box-shadow: $shadow;
    border-radius: .1875em;
    display: grid;
    grid-template-columns: calc(100% - 40.1875em) 40.1875em;
    overflow: hidden;
  }
  &__content{
    padding: 3.125em;
  }
  &__logo{
    width: 11.625em;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
  &__text{
    margin-top: 2.625em;
    @include grid100gap(1.25em);
    p{
      line-height: inherit;
    }
  }
}

.video{
  width: 100%;
  height: 100%;
  position: relative;
  &__img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
  &__play{
    @include centerAbsolute;
    width: 6.25em;
    height: 6.25em;
    border-radius: 50%;
    box-shadow: none;
    &:hover{
      color: $black2;
    }
    img{
      width: 2.375em;
      display: block;
    }
    span{
      position: absolute;
      top: 50%;
      left: 100%;
      transform: translateY(-50%);
      margin-left: .72em;
      font-size: .875em;
      line-height: 130%;
      width: 6em;
      font-weight: 700;
      text-transform: unset;
      text-align: left;
    }
  }
}

.why{
  padding: 5.25em 0 6.25em;
  background-image: url(../img/why-bg.svg);
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: left 2.5em top 0;
  &__items{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
    gap: 1.875em;
  }
  .fg{
    input{
      background-color: #fff;
      border-color: #fff;
    }
  }
}

.why-card{
  padding: 1.875em;
  background-color: $bg;
  border-radius: .1875em;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  &__icon{
    border-radius: 50%;
    width: 6.875em;
    height: 6.875em;
    border: 1px solid rgba($vinous,.5);
    padding: .3125em;
    &-inner{
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: $vinous;
      @include center;
    }
    img{
      width: 3.125em;
      display: block;
    }
  }
  &__title{
    margin-top: .8125em;
    min-height: 4.4375em;
    @media screen and (min-width: 993px){
      &:before{
        width: 5.5em;
      }
    }
  }
}

.book{
  background-color: $vinous;
  padding: 1.5em 1.875em 2em 10em;
  border-radius: .1875em;
  margin-top: 6.25em;
  position: relative;
  color: #fff;
  &__icon{
    position: absolute;
    top: 50%;
    left: 1.875em;
    transform: translateY(-50%);
    width: 6.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    display: flex;
    align-items: center;
  }
  &__split{
    margin: 0 .9em;
  }
}

.book-form{
  margin-top: 1.25em;
  display: grid;
  grid-template-columns: calc(50% - 15.3125em) calc(50% - 15.3125em) 12.5em 12.5em;
  gap: 1.875em;
}

.gallery{
  padding: 5.25em 0 6.25em;
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - .25em));
    gap: .375em;
  }
}

.gallery-card{
  &__img{
    display: block;
    @include imgCover(74.28%);
    img{
      border-radius: .1875em;
    }
  }
}

.more{
  margin-top: 1.875em;
  display: flex;
  justify-content: center;
}

.gallery-page{
  padding: 0 0 6.25em;
}

.consult{
  padding: 6.25em 0;
  background-image: url(../img/consult-bg.webp);
  background-size: cover;
  background-position: center;
  .book{
    margin: 0;
    background-color: #fff;
    &__title{
      color: $black;
    }
  }
}

.reviews{
  padding: 5.25em 0 6.25em;
  overflow: hidden;
  &__slider{
    margin-top: 1.875em;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
      overflow: hidden;
    }
    &-slide{
      padding-right: 1.875em;
    }
    &-nav{
      margin-top: 1.125em;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    &-pagination{
      width: unset;
      position: static;
      margin: 0 1.36em;
      @include sameNumWidth;
    }
    &-button{
      position: static;
      width: 1.875em;
      height: 1.875em;
      flex-shrink: 0;
      margin: 0;
      @include center;
      border: 1px solid $black;
      svg{
        width: .4375em;
        height: auto;
      }
      &:hover{
        border-color: $primary;
        background-color: $primary;
        path{
          stroke: #fff;
        }
      }
    }
  }
}

.review-card{
  padding: 1.875em;
  background-color: $bg;
  border-radius: .1875em;
  &__top{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 5em;
    height: 5em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  &__info{
    @include grid100gap(.4375em);
  }
  &__text{
    margin-top: 1.5em;
  }
}

.map{
  height: 31.25em;
  //-webkit-filter: grayscale(100%);
}

.ymaps-2-1-79-balloon__tail,
.ymaps-2-1-79-balloon__tail:after{
  display: none !important;
}

.ymaps-2-1-79-balloon{
  box-shadow: none !important;
  height: 2em;
  transform: translate(93px, 15px);
}

.ymaps-2-1-79-balloon__layout{
  box-shadow: $shadow3;
  border-radius: .3125em;
  border: none;
}

.ymaps-2-1-79-balloon__close+.ymaps-2-1-79-balloon__content{

  background: #FFFFFF;
}

.ymaps-2-1-79-balloon__content{
  font-size: 1em !important;
  min-height: unset !important;
  padding: .3125em .625em !important;
  &>*{
    display: flex !important;
    align-items: center;
    font-weight: 700;
    white-space: nowrap;
  }
}

.contacts{
  position: relative;

}

.contacts-info{
  z-index: 3;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  left: 3.4375em;
  background-color: rgba($vinous,.9);
  padding: 2.25em 1.875em 2.8125em;
  width: 28.25em;
  max-width: 100%;
  color: #fff;
  border-radius: .1875em;
  @media screen and (min-width: 1401px){
    left: calc(((100vw - (100vw - 100%)) - 1400px + 6.875em) / 2);
  }
  &__items{
    @include grid100gap(1.0625em);
  }
  &__item{
    display: flex;
    align-items: center;
  }
  &__icon{
    width: 1.25em;
    flex-shrink: 0;
    margin-right: .625em;
    svg{
      width: auto;
      height: auto;
      max-width: 100%;
      max-height: 1.25em;
    }
  }
  &__info{
    & *{
      line-height: inherit;
    }
    a{
      display: block;
    }
  }
  &__days{
    p{
      line-height: inherit;
    }
  }
  .socials{
    margin-top: 1.5em;
    &__item{
      margin: 0 1.25em 0 0;
      background-color: rgba(#fff,.1);
      &:hover{
        background-color: $primary;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 0;
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      position: relative;
      padding: 4.4375em 1.25em 6.9375em;
      max-width: unset;
      width: calc(100% + 2.5em);
      margin-left: -1.25em;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__info{
      gap: 2em;
      max-width: 24.375em;
    }
    &__desc{
      padding: .125em 0 .125em .625em;
    }
  }

  .main-form{
    padding: 1.25em;
    position: relative;
    z-index: 2;
    margin-top: -1.25em;
    box-shadow: $shadow2;
    @media screen and (min-width: 577px){
      grid-template-columns: repeat(4,calc(25% - .9375em));
      ._full{
        grid-column: span 2;
      }
    }
  }

  .about{
    padding: 3.75em 0;
    &__inner{
      grid-template-columns: calc(100% - 22.125em) 22.125em;
      box-shadow: $shadow2;
    }
    &__content{
      padding: 1.25em;
    }
    &__logo{
      width: 10em;
    }
    &__text{
      margin-top: 1em;
      gap: 1.0625em;
    }
  }

  .video{
    &__play{
      width: 3.75em;
      height: 3.75em;
      img{
        width: 1.5em;
      }
    }
  }

  .why{
    padding: 2em 0 3.75em;
    background-position: left 1.25em top 0;
    &__items{
      margin-top: 1.25em;
      grid-template-columns: repeat(4,calc(25% - .9375em));
      gap: 1.25em;
    }
  }

  .why-card{
    padding: 1.25em .625em;
    &__icon{
      width: 5em;
      height: 5em;
      padding: .2em;
      img{
        width: 2.3125em;
      }
    }
    &__title{
      margin-top: .3125em;
      min-height: 2.75em;
    }
    .text22{
      font-size: .875em;
      line-height: 130%;
    }
  }

  .book{
    margin-top: 3.75em;
    padding: .625em 1.25em 1.25em 7.5em;
    &__icon{
      width: 5em;
      left: 1.25em;
    }
    &__title{
      line-height: 130%;
    }
  }

  .book-form{
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
    margin-top: .875em;
  }

  .gallery{
    padding: 3em 0 3.75em;
    &__grid{
      margin-top: 1.25em;
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 1.25em;
    }
  }

  .more{
    margin-top: 1.25em;
  }

  .gallery-page{
    padding-bottom: 3.75em;
  }

  .consult{
    padding: 3.75em 0;
  }

  .reviews {
    padding: 3em 0 3.75em;
    overflow: hidden;
    &__slider {
      margin-top: 1.25em;
    }
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        padding-right: 1.25em;
      }
      &-nav{
        margin-top: 1.25em;
      }
    }
  }

  .review-card{
    padding: 1.25em;
    &__img{
      width: 3.75em;
      height: 3.75em;
      margin-right: .625em;
    }
    &__info{
      gap: 0;
    }
    &__text{
      margin-top: .9375em;
    }
  }

  .map{
    height: 26.875em;
  }

  .contacts-info{
    left: 1.25em;
    padding: .75em 1.25em 1.25em;
    width: 22.125em;
    &__items{
      gap: 1em;
    }
    .socials{
      margin-top: 1.5em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__content{
      padding-top: 2em;
      padding-bottom: 2em;
      min-height: 25.3125em;
      text-align: center;
    }
    &__img{
      ._view2{
        display: none;
      }
      ._view3{
        display: block;
      }
    }
    &__info{
      margin-top: .5em;
      gap: .625em;
    }
    &__desc{
      padding: 0 0 .875em;
      border: none;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        left: 50%;
        bottom: 0;
        transform: translateX(-50%);
        height: 1px;
        width: 3.125em;
        background-color: $primary;
      }
    }
  }

  .main-form{
    margin-top: -.625em;
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: 1.25em .625em;
    box-shadow: $shadow3;
  }

  .about{
    padding: 2em 0 2.5em;
    &__inner{
      grid-template-columns: 100%;
      box-shadow: $shadow3;
      margin-top: 1.25em;
    }
    &__content{
      padding-bottom: 0;
    }
    &__logo{
      width: 7.625em;
    }
    &__text{
      gap: 2em;
    }
  }

  .video{
    &__play{
      width: 2.5em;
      height: 2.5em;
      img{
        width: 1em;
      }
      span{
        font-size: .6875em;
        margin-left: 1em;
      }
    }
  }

  .why{
    padding: 2.125em 0 2.5em;
    background-image: unset;
    .text42{
      max-width: 10em;
      margin: 0 auto;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: .625em;
    }
  }

  .why-card{
    padding-left: .5125em;
    padding-right: .5125em;
    &__icon{
      width: 3.75em;
      height: 3.75em;
      padding: .168em;
      img{
        width: 1.75em;
      }
    }
  }

  .book{
    margin-top: 2.5em;
    padding: 1.25em;
    &__top{
      display: flex;
      align-items: center;
    }
    &__icon{
      width: 3.75em;
      position: static;
      transform: unset;
      flex-shrink: 0;
      margin-right: .625em;
    }
    &__info{
      @include grid100gap(.0625em);
      max-width: 7.75em;
    }
    &__split{
      display: none;
    }
  }

  .book-form{
    grid-template-columns: 100%;
    margin-top: 1.25em;
  }

  .gallery{
    padding: 2.125em 0 2.5em;
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .more{
    &__btn{
      width: 15em;
      max-width: 100%;
      padding: 0;
    }
  }

  .gallery-page{
    padding-bottom: 2.5em;
  }

  .consult{
    padding: 2.5em 0;
  }

  .reviews{
    padding: 2.125em 0 2.5em;
    .swiper{
      &-nav{
        margin-top: 1.0625em;
        justify-content: space-between;
        padding: 0 1.25em;
      }
      &-button{
        width: 1.5em;
        height: 1.5em;
        svg{
          width: .5em;
        }
      }
      &-pagination{
        font-size: 1.25em;
      }
    }
  }

  .contacts{
    height: 37.5em;
    overflow: hidden;
  }

  .map{
    height: 150%;
    width: 120%;
    margin-left: -20%;
    //margin-bottom: -20%;
  }

  .contacts-info{
    width: calc(100% - 2.5em);
    top: 1.25em;
    transform: unset;
    padding-top: .9375em;
    &__items{
      gap: .875em;
      max-width: 13.75em;
    }
    .socials{
      justify-content: unset;
      margin-top: 1em;
      &__item{
        margin-right: 1.875em;
      }
    }
  }

  .ymaps-2-1-79-balloon{
    //transform: unset;
  }
}
