.contacts-page{
  padding-bottom: 12.5em;
  &__grid{
    margin-top: 1.875em;
    display: grid;
    grid-template-columns: 28.125em calc(100% - 28.125em);
    box-shadow: $shadow;
    border-radius: .1875em;
    overflow: hidden;
    border: $border;
  }
  .contacts-info{
    position: static;
    transform: unset;
    background-color: #fff;
    color: $black;
    border-radius: 0;
    &__icon{
      path{
        fill: $black;
      }
    }
  }
  .socials{
    &__item{
      background-color: $primary;
      &:hover{
        background-color: $vinous;
      }
    }
  }
  .map{
    width: 100%;
    height: 100%;
  }
}


@media screen and (max-width: 992px){
  .contacts-page{
    padding-bottom: 6.25em;
    &__grid{
      grid-template-columns: 22.125em calc(100% - 22.125em);
      margin-top: 1.25em;
    }
  }
}

@media screen and (max-width: 576px){
  .contacts-page{
    padding-bottom: 2.5em;
    &__grid{
      grid-template-columns: 100%;
      position: relative;
    }
    .contacts-info{
      width: 100%;
    }
    &__wrap{
      height: 20em;
    }
    .map{
      top: 0;
      left: 0;
      //height: 20em;
      position: absolute;
      width: 120%;
      height: 150%;
      margin-left: -20%;
    }
  }
}
