.text42{
  font-size: 2.625em;
  line-height: 150%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  font-weight: 700;
}

.text22,
.text16,
.text14,
.text12{
  line-height: 150%;
  letter-spacing: 0.01em;
}

.text30{
  font-size: 1.875em;
  line-height: 130%;
  letter-spacing: 0.01em;
}

.text22{
  font-size: 1.375em;
}

.text16{
  font-size: 1em;
}

.text14{
  font-size: .875em;
}

.text12{
  font-size: .75em;
}

.shadow-text{
  text-shadow: 0 .125em .625em rgba(0, 0, 0, 0.1);
}

.bold-text{
  font-weight: 700;
}

.light-text{
  font-weight: 300;
}

.primary-text{
  color: $primary;
}

.black-text2{
  color: $black2;
}


@media screen and (max-width: 992px){
  .text42{
    font-size: 1.875em;
  }

  .text30{
    font-size: 1.25em;
  }

  .text22{
    font-size: 1.25em;
  }

  .text16{
    font-size: .875em;
  }
}


@media screen and (max-width: 576px){
  .text42{
    font-size: 1.25em;
  }

  .text30,
  .text22{
    font-size: .875em;
  }

  .text16{
    font-size: .6875em;
  }
}
